<template>

  <div class="users">
    <div v-if="!isHidden" class="modal-window">
    <div style="text-align: center; border-radius: 25px;">
      <h3> <img class="text-icon" src="/img/check_box-black-18dp.svg">{{msg}}</h3>
    </div>
    </div> 

    <form class="pure-form pure-form-aligned">
      <fieldset>
        <div class="pure-control-group">
          <label for="aligned-name">Username</label>
          <input type="text" disabled="" id="aligned-name" placeholder="admin" />
        </div>
        <div class="pure-control-group">
          <label for="aligned-password">Password</label>
          <input type="password" id="password1" placeholder="Password" v-model="password1" />
        </div>
        <div class="pure-control-group">
          <label for="aligned-password"></label>
          <input type="password" id="password2" placeholder="Password again" v-model="password2" />
        </div>
        <div class="pure-controls">
          <button v-on:click="submit()" type="button" class="pure-button pure-button-primary">Submit</button>
        </div>
    </fieldset>
  </form>
  
</div>
</template>

<script>

export default {
  name: 'Users',
  props: {
  },
  data() {
    return {
      password1: "",
      password2: "",
      isHidden: true,
      msg: "",
    };
  },
  created() {
  },
  methods: {
    submit() {

      this.$http.put('/api/v1/account/admin', {
        password1: this.password1,
        password2: this.password2,
      })
      .then((response) => {
        if(response.status == 200) {
          this.msg = "Update successfully";
        }

      })
      .catch((error) => {
        if (error.response) {
          this.msg = error.response.data;
        }
      })
      .then(() => {
        this.isHidden = false;
        setTimeout(() => { this.isHidden = true; }, 2000);
      });
    },
  },
}
</script>

