<template>
<div>
  <div class="container">
    <h4 style="border-bottom: 1px solid gray"> Device Information </h4>
    <p> <span class="title">IP Address</span>: {{ipAddr}} </p>
  </div>

  <div class="container">
    <h4 style="border-bottom: 1px solid gray"> Logout </h4>
    <button v-on:click="onClickSetting('Logout', logout)" class="pure-button"><img src="/img/login-black-18dp.svg"></button>
  </div>

  <div class="container">
    <h4 style="border-bottom: 1px solid gray"> Reset to Default </h4>
    <button v-on:click="onClickSetting('Reset to default', resetToDefault)" class="pure-button"><img src="/img/sync-black-18dp.svg"></button>
  </div>


  <div v-if="!isHidden" class="modal-window">
    <div style="text-align: center;">
    <h4> {{message}}. Are you sure? </h4>
    <button v-on:click="onClickYes" class="pure-button pure-button-primary">Yes</button>
    <button v-on:click="isHidden=true" class="pure-button pure-button-primary" style="margin-left:20px">No</button>
    </div>
  </div>


</div>
</template>

<script>
export default {
  name: 'Dashboard',
  props: {
  },
  data() {
    return {
      ipAddr: "",
      isHidden: true,
      message: "",
    }
  },
  created() {
    this.ipAddr = "192.168.11.1";

    this.$http.get('/api/v1/network/wireless/connection', {},
    )
    .then((response) => {
      this.ipAddr = response.data.ipAddr;
      console.log(response.data);
    })
    .catch((error) => {
      console.log(error);
    })
    .then(() => {
      this.isLoading = false;
    });

  },
  methods: {
    onClickSetting(message, callback) {
      this.isHidden = false;
      this.message = message;
      this.action = callback;
    },
    onClickYes() {
      this.action();
      this.isHidden = true;
    },
    resetToDefault() {
      this.$http.post('/api/v1/system/command/reset', {},
      )
      .then(() => {
        location.reload();
      })
      .catch(() => {
      })
      .then(() => {
      });

    },
    logout() {
      localStorage.removeItem("xiaoPiToken");
      sessionStorage.removeItem("xiaoPiToken");
      location.reload();
    }

  },
}
</script>

<style scoped>
.title {
  display:inline-block;
  width:100px; 
}
</style>
